@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.25em;
    height: 3.5em;
    border-radius: 3.125em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $black;
    &:hover{
        background-color: darken($primary,5%);
        color: $black;
    }
}

.m-btn-primary-outline{
    color: $black;
    border: 1px solid $primary;
    &:hover{
        background-color: $primary;
        color: $black;
    }
}
