@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
._mt30{
  margin-top: 1.875em;
}

._mt16{
  margin-top: 1em;
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($black,.2);
    pointer-events: none;
    border-radius: inherit;
  }
}
._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}


.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: #1A1A1A;
    @include lastM(0 .75em 0 0);
    @include center;
    &:hover{
      path{
        fill-opacity: 1;
        &:not(._not-fill){
          fill: #fff;
        }
      }
    }
    &._vk,
    &._tg{
      &:hover{
        background: #07F;
        border-color: #07F;
      }
    }
    &._whatsapp{
      &:hover{
        background: #48C95F;
        border-color: #48C95F;
      }
    }
    &._youtube{
      &:hover{
        background: #F40000;
        border-color: #F40000;
        ._not-fill{
          fill: #F40000;
        }
      }
    }
  }
}

