.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .4;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 7.5em 0 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 40em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 4em 4em 3.625em;
        border-radius: 1.25em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        bottom: 100%;
        right: 0;
        margin: 0 .625em .625em 0;
    }
    &__btn{
        margin-top: 2.1875em;
        width: 100%;
        font-weight: 600;
    }
}

.close-btn{
    width: 1.875em;
    height: 1.875em;
    @include center;
    border-radius: 50%;
    &:hover{
        background-color: $primary;
    }
    img{
        width: 1.125em;
    }
}

.cities{
    &__items{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-height: 28.75em;
        overflow-y: auto;
        @include scroll;
    }
    &__item{
        padding: .25em .5em;
        font-family: $font2;
        display: flex;
        align-items: center;
        @include lastM(0 0 .125em);
        transition: all .3s;
        cursor: pointer;
        border-radius: 3.125em;
        span{
            font-size: 1.25em;
            line-height: 135%;
        }
        &._active{
            background: #f0f5d2;
            color: $black;
            span{
                text-decoration: underline;
            }
        }
    }
    &__nav{
        display: none;
    }
}



@media screen and (max-width: 992px){
    .modal{
        padding-top: 2em;
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            max-width: 100%;
            width: 100%;
            position: fixed;
            bottom: 0;
            left: 0;
            border-radius: 1.25em 1.25em 0 0;
            padding: 2.5em .6875em 1.875em;
        }
        &__top{
            padding: 0 2.375em;
        }
        .close-btn-mob{
            bottom: unset;
            right: 50%;
            top: .875em;
            margin: 0;
            transform: translateX(50%);
        }
    }

    .close-btn-mob{
        background: $primary;
        width: 4.0625em;
        height: .375em;
        border-radius: .625em;
        img{
            display: none;
        }
    }

    .cities{
        position: relative;
        &__items{
            max-height: 28em;
            padding-bottom: 2em;
        }
        &__item{
            &._active,
            &:hover{
                background-color: $primary;
                span{
                    text-decoration: none;
                }
            }
        }
        &__nav{
            position: absolute;
            left: -.625em;
            bottom: -.25em;
            width: 100%;
            padding: 0 .625em;
            display: block;
            background-color: #fff;
        }
        &__btn{
            width: 100%;
        }
    }
}
