.header{
  padding: 1.875em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10.8125em;
    margin-right: 1.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.nav{
  .menu{
    display: flex;
    align-items: center;
    li{
      margin-right: 1.875em;
      &:last-child{
        margin-right: 0;
      }
    }
    a{
      &:hover{
        text-decoration: underline;
      }
    }
    .current-menu-item{
      a{
        background-color: $primary;
        @media screen and (min-width: 993px){
          border-radius: 3.125em;
          padding: 0 .5em;
          height: 1.875em;
          @include center;
        }
      }
    }
  }
}

._flex,
._flex-between{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

.header-action{
  margin-left: auto;
  padding-left: 1em;
  &__login{
    padding: 0 1.1875em;
    height: 2.875em;
    img{
      width: 1.6875em;
      margin-right: 1.25em;
    }
  }
}

.header-search{
  margin-right: 2em;
  &__show{
    display: none;
  }
  &__hidden{
    width: 28.75em;
  }
}

.search-field{
  input{
    height: 2.875em;
    padding-left: 2.75em;
    border-radius: .625em;
  }
  &__icon{
    position: absolute;
    top: 50%;
    left: .75em;
    transform: translateY(-50%);
    width: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__inner{
      flex-wrap: wrap;
      position: relative;
    }
    &__logo{
      width: 7.875em;
    }
  }

  .nav{
    order: 2;
    width: 100%;
    margin-top: .875em;
    .menu{
      li{
        flex-grow: 1;
        margin-right: .625em;
      }
      a{
        @include center;
        width: 100%;
        height: 2.86em;
        font-size: .875em;
        border-radius: 1.43em;
        border: 1px solid $primary;
        &:hover{
          background-color: $primary;
          color: $black;
          text-decoration: none;
        }
      }
    }
  }

  .header-search{
    margin-right: .75em;
    &._open{
      .header-search__show{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .header-search__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      display: flex;
      width: 2.875em;
      height: 2.875em;
      border-radius: 50%;
      padding: 0;
      position: relative;
      img{
        width: 1.4375em;
        @include centerAbsolute;
      }
      ._view2{
        opacity: 0;
      }
    }
    &__hidden{
      width: 100%;
      position: absolute;
      top: 3.6875em;
      right: 0;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
  }

  .header-action{
    &__login{
      padding: 0 .75em;
      span{
        font-size: .875em;
      }
      img{
        width: 1.4375em;
        margin-right: .375em;
      }
    }
  }
}


